@import "../settings/variables";

.home__hero {
  height: calc(963px - 37px - 95px);
}

.hero {
  height: 100%;
  width: 100%;
}

.row {
  height: 100%;
  width: 100%;
}

.col-6 {
  height: 100%;
  width: 50%;
}

.hero--left {
  width: 50%;
  position: relative;
  img {
    position: absolute;
    overflow: hidden;
    z-index: -1;
    bottom: 0;
    left: -483px;
    width: 1368px;
    height: 963px;
  }
}

.hero--right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.hero__title {
  font-size: 38px;
}

.hero__title--first {
  margin-bottom: 30px;
}

.hero__title--second {
  font-size: 38px;
  margin-bottom: 25px;
  position: relative;
}

.hero__title--separator{
  margin-bottom: 65px;
}

.hero__list {
  display: flex;
}

.list__item {
  margin-right: 22px;
  margin-left: 22px;
}

.list__btn {
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  height: 120px;
  width: 310px;
  text-transform: uppercase;
  font-size: 36px;
  font-weight: lighter;
  color: $color-dark-grey;
  text-align: center;
  border: 0.75px solid $color-dark-grey;
  border-radius: 0;
}